import AffixList from '../data/AffixList';
import characters from '../data/characters';
import React, { useEffect, useRef } from 'react';
import { useState} from 'react';
import Select from 'react-select'
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Result from './Result';
import '../css/simulator.css';
import AffixName from '../data/AffixName';

//
function Simulator(){
    //
    const version="1.1";

    // 
    const [partsIndex,setPartsIndex]=useState(undefined);
    const [MainSelectOptions,setMainSelectOptions]=useState();
    
    const [ExpRate,setExpRate]=useState(undefined);
    const [Rscore,setRscore]=useState(undefined);
    const [Rrank,setRank]=useState({color:undefined,rank:undefined});
    const [statusMsg,setStatusMsg]=useState(undefined);
    const [processBtn,setProcessBtn]=useState(true);
    const standDetails=useRef([]);

    const SubData=useRef([]);
    const [charID,setCharID]=useState(undefined);
    const [PieNums,setPieNums]=useState(undefined);

    //
    const [selfStand,setSelfStand]=useState([]);

    //
    const [relic,setRelic]=useState();

    //
    //const [historyData,setHistoryData]=useState([]);
    const historyData=useRef([]);
    const partArr=['Head ','Hand ','Body ','Feet ','Rope ','Ball '];
    
    //()
    const [isSaveAble,setIsSaveAble]=useState(false);
    const [isChangeAble,setIsChangeAble]=useState(true);

    //
    const location = useLocation();

    useEffect(()=>{
        init();
    },[location])

    useEffect(() => {
        if(partsIndex!==undefined&&Number.isInteger(partsIndex)){
            let range=AffixList.find((s)=>s.id===(parseInt(partsIndex))).main;
            const targetAffix = range[0];
            setMainSelectOptions(targetAffix); 
        }
    }, [partsIndex]); 

    function init(){
        SubData.current=[];
        for(var i=0;i<=3;i++){
            let data={
                index:i, 
                subaffix:0,//
                data:0, //
                count:0 //
            }

            SubData.current.push(data);
        }
        
        let history=JSON.parse(localStorage.getItem('HistoryData'));
        
        // 
        // 
        
        if(history!=null&&history.length>0){
            history=history.filter((h)=>h.version===version);
            localStorage.setItem('HistoryData',JSON.stringify(history));
            //setHistoryData(prev=>prev != history ? history : prev);
            historyData.current=history;
            setStatusMsg('!!');
        }
    }


    function updateSubAffix(val,index){
        SubData.current.find((s,i)=>i===parseInt(index)).subaffix=val;
    }

    function updateSubData(val,index){
        SubData.current.find((s,i)=>i===parseInt(index)).data=Number(val);
    }

    function updateSubCount(val,index){
        SubData.current.find((s,i)=>i===parseInt(index)).count=Number(val);
    }

    //
    function updateHistory(index){
        // 
        historyData.current=historyData.current.filter((item,i)=>i!==index);
        setStatusMsg('.....');
        //
        setTimeout(() => {
            setStatusMsg('!!');
        }, 0);
        localStorage.setItem('HistoryData',JSON.stringify(historyData.current));
    }
    //
    function clearData(){
        setExpRate(undefined);
        setRank({color:undefined,rank:undefined});
        setPieNums(undefined);
        setRscore(undefined);
        setRelic(undefined);
    }

    //
    function saveRecord(){
        let partName=partArr[partsIndex-1];
        let selectChar=characters.find((c)=>c.charID===charID);

        // 
        if(!historyData.current)
            historyData.current=[];
        else if(historyData.current.length>=6)//6 
            historyData.current=historyData.current.filter((item,i)=>i!==0);
        
        //
        if(!PieNums||ExpRate===undefined||!Rrank||Rscore===undefined){
            setStatusMsg("!!");
            return;
        }
         //
        if(!charID){
            setStatusMsg("!!");
            return;
        }


        //
        let data={
            version:version,
            char:selectChar,
            part:partName,
            mainaffix:MainSelectOptions,
            expRate:ExpRate,
            score:Rscore,
            rank:Rrank,
            pieData:PieNums,
            stand:standDetails.current,
            relic:relic
        };

        //
        let oldHistory=JSON.parse(JSON.stringify(historyData.current));
        historyData.current.push(data);
        setStatusMsg('');
        const targetElement = document.getElementById('historyData');
        targetElement.scrollIntoView({ behavior: 'smooth' });
        

        //
        oldHistory.push(data);
        localStorage.setItem('HistoryData',JSON.stringify(oldHistory));
        setIsSaveAble(false);
    }

    //
    function checkDetails(index){
        let data=historyData.current[index];
        //console.log(data);
        setRank(data.rank);
        setExpRate(data.expRate);
        setRscore(data.score)
        setStatusMsg('!!');
        setPieNums(data.pieData);
        standDetails.current=data.stand;
        setRelic(data.relic);

        document.getElementById("resultDetails").scrollIntoView({ behavior: "smooth" });
    }

    //
    function saveRelic(){
        let data={
            main_affix:MainSelectOptions,
            subaffix:[]
        }

        SubData.current.forEach((s,i)=>{
            if(!['','','',''].includes(s.subaffix))
                s.display=s.data+'%';
            else
                s.display=s.data;
        })
        data.subaffix=SubData.current;

        setRelic(data);
    }

    //
    const PartSelect=()=>{
        
        let options=[<option value={'undefined'} key={'PartsUndefined'}></option>];

        partArr.forEach((a,i)=>{
            options.push(
                <option value={i+1} key={'Parts'+i} >{a}</option>       
            )
        })

        return(
            <select value={partsIndex} 
                    onChange={(event)=>setPartsIndex(event.target.value)}
                    disabled={!isChangeAble}
                    className='w-[120px]'>{options}</select>
        )
    }

    const MainAffixSelect=()=>{
        if(Number.isInteger(parseInt(partsIndex))&&partsIndex!==undefined){
            let range=AffixList.find((s)=>s.id===(parseInt(partsIndex))).main;
            
            //
            if(range.length===1){
                setMainSelectOptions(range[0]);
                return(<span className='text-white'>{MainSelectOptions}</span>)
            }else{
                //
                let options=[<option value={'undefined'} key={"MainAfffixUndefined"}></option>];

                range.forEach((s,i)=>{
                    options.push(<option value={s} key={'Mainaffix'+i}>{s}</option>)
                });

                return(<select  defaultValue={MainSelectOptions} 
                                onChange={(event)=>setMainSelectOptions(event.target.value)}
                                disabled={!isChangeAble}
                                className='w-[120px]'>{options}</select>)
            }
        }else{
            return(<></>)
        }
    }

    const SubAffixSelect=({index})=>{
        if(MainSelectOptions!==undefined&&MainSelectOptions!=='undefined'){
            let range=AffixList.find((s)=>s.id===parseInt(partsIndex)).sub;
            let options=[<option value={'undefined'} key={`SubaffixUndefined`}></option>];

            range.forEach((s,i)=>{
                options.push(<option value={s} key={`Subaffix${i}`}>{s}</option>)
            });
            

            return(<div className='my-1' key={'SubAffixSelect'}>
                <select defaultValue={SubData.current[index].subaffix} 
                        onChange={(event)=>updateSubAffix(event.target.value,index)} 
                        className=''
                        disabled={!isChangeAble}>
                            {options}

                </select>
                <input type='number' defaultValue={SubData.current[index].data}
                        onChange={(event)=>updateSubData(event.target.value,index)}
                        className='ml-2 max-w-[70px] pl-2' 
                        disabled={!isChangeAble} min={0} title=''/>
                <input type='number' defaultValue={SubData.current[index].count}
                        onChange={(event)=>updateSubCount(event.target.value,index)}
                        className='ml-2 text-center' disabled={!isChangeAble}
                        min={0} max={5} title=''/>
                </div>)
        }else{
            return(<></>)
        }   
    }

    //
    const CharSelect=()=>{
        let options=[];
        
        characters.forEach((c)=>{
            options.push({
                value: c.charID, 
                label: c.name,
                icon: `https://raw.githubusercontent.com/Mar-7th/StarRailRes/master/icon/character/${c.charID}.png`
            })
        })

        //
        const customFilterOption = (option, inputValue) => {
            return option.data.label.toLowerCase().includes(inputValue.toLowerCase());
        };
        
        const selectedOption = options.find((option) => option.value === charID);
        return(<Select options={options} 
                    className='w-[200px]' 
                    onChange={(option)=>setCharID(option.value)}
                    value={selectedOption} 
                    isDisabled={!isChangeAble}
                    getOptionLabel={(e) => (
                        <div style={{ display: "flex", alignItems: "center"  }}>
                          <img src={e.icon} alt={e.label} style={{ width: 30, height: 30, marginRight: 8 ,borderRadius:"25px" }} />
                          {e.label}
                        </div>
                    )}
                    filterOption={customFilterOption}/>)
    }

    //
    const ShowStand=()=>{
        const list=selfStand.map((s,i)=><>
            <div className='flex flex-row my-0.5'>
                <div className='flex justify-between w-[200px] mt-0.5 max-[800px]:w-[130px]'>
                    <span className='whitespace-nowrap overflow-hidden text-ellipsis'>{s.name}</span>
                    <input type='number' min={0} max={1} 
                        className='ml-2 text-center min-w-[40px]' defaultValue={selfStand[i].value}
                        title='0 1'
                        onChange={(event)=>changeVal(i,event.target.value)}
                        disabled={!isChangeAble}/>
                    
                </div>
                <button onClick={()=>removeAffix(i)} className='deleteBtn ml-2 px-1' disabled={!isChangeAble}></button>
            </div>
        </>)

        function removeAffix(index){
            setSelfStand((arr)=>arr.filter((item,i)=>i!==index));
        }

        function changeVal(index,val){
            let stand=selfStand;
            selfStand[index].value=val;

            setSelfStand(stand);
        }

        return(
            <div className='flex flex-col'>
                {list}
            </div>
        )
    }

    //
    const PastPreview=({index})=>{
        let data=historyData.current[index];

        const hue = data.expRate * 120;
        
        const textColor =`hsl(${hue}, 100%, 50%)`;
        let BaseLink=`https://raw.githubusercontent.com/Mar-7th/StarRailRes/master/icon/character/${data.char.charID}.png`;

        return(<>
            <div className='flex flex-row flex-wrap w-[300px] max-h-[120px] bg-slate-700 rounded-md p-2 m-2'>
                <div className='flex flex-col mr-3'>
                    <div>
                        <img src={BaseLink} alt='iconChar' className='w-[70px] rounded-[50px]'/>
                    </div>
                    <div className='text-center'>
                        <span style={{color:data.rank.color}} className='font-bold text-xl'>{data.score}</span>
                    </div>
                </div>
                <div className='flex flex-col'>
                    <div>
                        <span className='text-white'>:{data.part}</span>
                    </div>
                    <div>
                        <span className='text-white'>:{data.mainaffix}</span>
                    </div>
                    <div>
                        <span className='text-white'>:
                            <span style={{color:textColor}} className='pl-1 font-bold'>{(data.expRate*100).toFixed(1)}%</span>
                        </span>
                    </div>
                    <div>
                        <button className='processBtn mr-2 px-1' onClick={()=>checkDetails(index)}></button>
                        <button className='deleteBtn px-1' onClick={()=>updateHistory(index)}></button>
                    </div>
                </div>
            </div>
        
        </>)
    };

    //
    const RelicData=()=>{
        if(relic!==undefined){
            
            const list=[];

            relic.subaffix.forEach((s)=>{
                let isBold=(standDetails.current.find((st)=>st.name===s.subaffix)!==undefined)?true:false;
                
                let markcolor="";

                var IconName = AffixName.find((a)=>a.name===s.subaffix).icon;
                console.log(IconName);

                var imglink=`https://raw.githubusercontent.com/Mar-7th/StarRailRes/master/icon/property/${IconName}.png`;
                switch(s.count){
                    case 0:
                        markcolor='rgb(122, 122, 122)';
                        break;
                    case 1:
                        markcolor='rgb(67, 143, 67)';
                        break;
                    case 2:
                        markcolor='rgb(23, 93, 232)';
                        break;
                    case 3:
                        markcolor='rgb(67, 17, 184)';
                        break;
                    case 4:
                        markcolor='rgb(219, 171, 15)';
                        break;
                    default:
                        break;
                }
                list.push(
                    <div className='flex flex-row' key={'Data'+s.subaffix}>
                        <div className='flex justify-center items-center'>
                            <span className='mr-0.5 text-white w-[20px] h-[20px] rounded-[20px]
                                flex justify-center items-center' style={{backgroundColor:markcolor}}>
                                {s.count}
                            </span>
                        </div>
                        <div className='w-[160px] flex flex-row'>
                            <div className='flex justify-center items-center'>
                                <img src={imglink} alt='555' width={24} height={24}/>
                            </div>
                            <span className={`${(isBold)?'text-yellow-500 font-bold':'text-white'} text-left flex` }>{s.subaffix}</span>
                        </div>
                        <span className='flex w-[80px]'>:<span className='ml-2 text-white '>{s.display}</span></span>
                    </div>    
                )
            })
            
            return(
                <div className={`w-[100%] mb-5 border-t-4 border-gray-600 my-2 pt-2 
                    ${(statusMsg!==undefined)?'':'hidden'} max-[500px]:min-w-[330px]`}>
                    <div>
                        <span className='text-red-600 text-lg font-bold'></span>
                    </div>
                    <div className='mt-1'>
                        <span></span><br/>
                        <span className='text-white'>{relic.main_affix}</span>   
                    </div>
                    <div className='mt-2'>
                        <span></span>
                        <div className='flex flex-col w-[200px]'>
                            {list}
                        </div>
                    </div>
                </div>
            )
        }else{
            return(<></>)
        }
    }
        
    function calScore(){
        //
        //?
        //?
        let errors=false;

        SubData.current.some((s,i)=>{
            if(s.subaffix===MainSelectOptions){
                alert(`${i+1}\n\n!!`);
                errors=true;
                return true;
            }
            else if(s.subaffix==='undefined'||s.subaffix===0){
                alert(`\n!!`);
                errors=true;
                return true;
            }
        })

        if(errors) return;
        //?
        const seen = new Set();
        for (const obj of SubData.current) {
            if (seen.has(obj['subaffix'])) {
                alert(`\n!!`);
                errors=true;
                return;
            }
            seen.add(obj['subaffix']);
        }

        //
        selfStand.forEach((s)=>{
            if(s.value===''){
                errors=true;
                alert('');
            }
                
        })

        if(errors) return;

        // 0.5 
        let deviation=(SubData.current.includes((s)=>s.subaffix==='spd'))?0.5*(selfStand.find((s)=>s.name==='').value):0;
        SubData.current.forEach(s=>{
            if(s.subaffix!=='spd'&&s.count!==0)// 0.2
                deviation+=0.2;
        })
        
        //
        let worker=new Worker(new URL('../worker/worker.js', import.meta.url));
        let postData={
            charID:charID,
            MainData:MainSelectOptions,
            SubData:SubData.current,
            partsIndex:partsIndex,
            standard:selfStand,
            deviation:deviation
        };

        //disable
        setIsSaveAble(false);
        setProcessBtn(false);
        setIsChangeAble(false);
        setStatusMsg('!!');
        clearData();
        worker.postMessage(postData);

        //  Worker 
        worker.onmessage = function (event) {
            
            setExpRate(event.data.expRate);
            setRscore(event.data.relicscore)
            setStatusMsg('!!');
            setPieNums(event.data.returnData);
            setRank(event.data.relicrank);
            saveRelic();
            standDetails.current=selfStand;
            
            //
            setProcessBtn(true);
            setIsSaveAble(true);
            setIsChangeAble(true);
        };
    }

    const HistoryList=()=>{
        if(historyData.current){
            return(
                historyData.current.map((item,i)=>
                    <PastPreview index={i} key={'historyData'+i}/>
                )
            )
        }else{
            return <></>
        }
    }

    const StandardSelect=()=>{
        const [selectAffix,setAffix]=useState(undefined);
        
        //  
        function addAffix(){
            if(selectAffix===undefined)
                return;
            let newItem={
                name:selectAffix,
                value:1
            }

            if(selfStand.length<6&&!(selfStand.findIndex((item)=>item.name===selectAffix)>=0))
                setSelfStand((old)=>[...old,newItem]);
        }

        function clearAffix(){
            setSelfStand([]);
        }

        if(partsIndex!==undefined){
            // 
            let target=AffixList.find((a)=>a.id===parseInt(partsIndex));
            // 
            let mergedArray = [...new Set([...target.main, ...target.sub])];
            mergedArray=mergedArray.filter((item)=>item!==''&&item!==''&&item!=='')

            //console.log(mergedArray);
            let options=[<option value={'undefined'} key={'PartsUndefined'}></option>];

            mergedArray.forEach((a,i)=>{
                options.push(
                    <option value={a} key={'Affix'+i} >{a}</option>       
                )
            });

            return(
                <div className='flex flex-row flex-wrap items-center'>
                    <select value={selectAffix} 
                        onChange={(event)=>{setAffix(event.target.value)}}
                        disabled={!isChangeAble} className='mr-1 h-[25px]'>{options}</select>
                    <div className='max-[520px]:mt-1'>
                        <button className='processBtn px-1' onClick={addAffix} disabled={!isChangeAble}></button>
                        <button className='deleteBtn ml-1 px-1' onClick={clearAffix} disabled={!isChangeAble}></button>
                    </div>
                </div>
                
            )
        }else{
            return(<></>)
        }

    }

    //?
    const StandDetails=()=>{
    
        if(standDetails.current!==undefined){
            const list=standDetails.current.map((s,i)=>{
                var IconName = AffixName.find((a)=>a.name===s.name).icon;

                var imglink=`https://raw.githubusercontent.com/Mar-7th/StarRailRes/master/icon/property/${IconName}.png`;
                
                return(
                    <div className='flex flex-row' key={'StandDetails'+i}>
                        <div className='flex justify-between w-[15vw] min-w-[150px] mt-0.5'>
                            <div className='flex flex-row'>
                                <img src={imglink} alt="icon" width={24} height={24}/>
                                <span>{s.name}</span>
                            </div>
                            <span>{s.value}</span>
                        </div>
                    </div>
                );
            })

            return(<>
                <div className={`w-[100%] mb-5 border-t-4 border-gray-600 my-2 pt-2 
                    max-[600px]:!min-w-[0px]`}>
                    <div>
                        <span className='text-red-600 text-lg font-bold'></span>
                    </div>
                    <div>
                        {list}
                    </div>
                </div>
            
            </>)
        }
    }
    
    return(<>
        <div className='w-4/5 mx-auto max-[600px]:w-[90%]'>
            <Helmet>
                <title>--</title>
                <meta name="description" content="--" />
                <meta name="keywords" content="" />
            </Helmet>
            <h1 className='text-red-500 font-bold text-2xl'></h1>
            <div className='flex flex-row flex-wrap'>
                <div className='flex flex-col mt-2 w-1/2 max-[800px]:w-[100%]'>
                    <div className='flex flex-row [&>*]:mr-2 my-3 items-center max-[400px]:!flex-col max-[400px]:items-start'>
                        <div className='text-right w-[200px] max-[600px]:max-w-[150px] max-[400px]:text-left'>
                            <span className='text-white'>Characters :</span>
                        </div>
                        <CharSelect />
                    </div>
                    <div className={`my-1 ${(Number.isInteger(charID)&&charID!==undefined)?'':'hidden'} mt-2 [&>*]:mr-2 flex flex-row max-[400px]:!flex-col max-[400px]:items-start`}>
                        <div className='text-right w-[200px] max-[600px]:max-w-[150px] max-[400px]:text-left'>
                            <span className='text-white'>Parts :</span>
                        </div>
                        <PartSelect />   
                    </div>
                    <div className={`my-1 ${(Number.isInteger(parseInt(partsIndex)))?'':'hidden'} mt-2 [&>*]:mr-2 flex flex-row max-[400px]:items-start max-[400px]:!flex-col`}>
                        <div className='text-right w-[200px] max-[600px]:max-w-[150px] max-[400px]:text-left'>
                            <span className='text-white'>MainAffix :</span>
                        </div>
                        <MainAffixSelect />
                    </div>
                    <div className={`my-1 ${(MainSelectOptions!==undefined&&MainSelectOptions!=='undefined')?'':'hidden'} 
                            mt-2 [&>*]:mr-2 flex flex-row max-[600px]:!flex-col max-[600px]:text-center max-[400px]:text-left`}>
                        <div className='text-right w-[200px] max-[600px]:w-[100%] max-[600px]:text-center max-[400px]:text-left'>
                            <span className='text-white'>SubAffix :</span>
                        </div>
                        <div className='flex flex-col'>
                            <SubAffixSelect index={0}/>
                            <SubAffixSelect index={1}/>
                            <SubAffixSelect index={2}/>
                            <SubAffixSelect index={3}/>
                        </div>
                    </div>
                    <div className={`mt-2 [&>*]:mr-2 flex flex-row items-baseline max-[400px]:!flex-col ${(partsIndex===undefined)?'hidden':''}`}>
                        <div className='text-right w-[200px] max-[600px]:max-w-[150px] max-[400px]:text-left'>
                            <span className='text-white'>Affix :</span>
                        </div>
                        <StandardSelect />
                    </div>
                    <div className={`mt-2 [&>*]:mr-2 flex flex-row max-[400px]:!flex-col ${(selfStand.length===0)?'hidden':''}`} >
                        <div className='text-right w-[200px] max-[600px]:max-w-[150px] max-[400px]:text-left'>
                            <span className='text-white'>Params :</span>
                        </div>
                        <ShowStand />
                    </div>
                    <div className={`${(Number.isInteger(parseInt(partsIndex)))?'':'hidden'} mt-2 mb-2 max-w-[400px]  
                                flex flex-row [&>*]:mr-2 justify-end max-[400px]:justify-start`}>
                            <div className='flex flex-row mt-1'>
                                <button className='processBtn mr-2 whitespace-nowrap' 
                                    onClick={calScore} 
                                    disabled={!processBtn}></button>
                                <button className='processBtn mr-2 whitespace-nowrap' 
                                onClick={saveRecord} disabled={!isSaveAble}></button>
                            </div>
                        
                    </div>
                </div>
                <div className='w-1/2 max-w-[400px] flex flex-col max-[800px]:w-[100%] max-[600px]:my-3'>
                    <h2 className='text-red-600 font-bold text-lg'></h2>
                    <ul className='[&>li]:text-white list-decimal [&>li]:ml-2'>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li>!</li>
                        <li>
                        <a href='https://home.gamer.com.tw/artwork.php?sn=6065608' className='!underline'></a></li>
                    </ul>
                </div>
            </div>
            <div className='flex flex-row mb-3 flex-wrap'>
                <div className={`w-[100%] max-[930px]:w-[100%] border-t-4 border-gray-600 p-2 my-4 ${(!historyData.current||historyData.current.length===0)?'hidden':''}`}
                    id="historyData" >
                    <div>
                        <span className='text-red-500 text-lg font-bold'></span>
                    </div>
                    <div className='flex flex-row flex-wrap h-[300px] overflow-y-scroll hiddenScrollBar max-[600px]:!flex-col max-[600px]:!flex-nowrap'>
                        <HistoryList />
                    </div>
                </div>
                <div className={`mt-3 flex flex-row flex-wrap w-[18vw]  max-[700px]:w-[50%] ${(PieNums===undefined)?'hidden':''}`} >
                    <RelicData />
                </div>
                <div className={`mt-3 w-1/4 max-[700px]:w-[50%] ${(PieNums===undefined)?'hidden':''}`} >
                    <StandDetails />
                </div>
                <div className='mt-3 flex flex-row flex-wrap w-1/2 max-[700px]:w-[100%]' id="resultDetails">
                    <Result ExpRate={ExpRate} 
                            Rscore={Rscore} 
                            statusMsg={statusMsg} 
                            Rrank={Rrank} 
                            PieNums={PieNums}/>
                </div>
                
            </div>
        </div>
    
    </>)


}

export default Simulator;

